import React, { Component } from "react";
import styled from "styled-components";
import { data } from "../../pages/data.json";

const Div = styled.div`
  background: #f2f7fa;
`;

const Content = styled.div`
  max-width: 980px;
  padding: 35px 15px;
  margin: 0 auto;
  .more-title {
    border-bottom: 1px solid #e9e8e7;
    padding-bottom: 10px;
  }
`;

class MoreRelation extends Component {
  componentWillMount = () => {
    const length = data.length;
    console.log("總共有", length);
    const app = this.getRandom(length);
    console.log(app);
  };

  getRandom = x => {
    return Math.floor(Math.random() * x);
  };

  render() {
    return (
      <Div>
        <Content>
          <h2 className="more-title">更多相關的文章</h2>
        </Content>
      </Div>
    );
  }
}

export default MoreRelation;
